import "./index.scss";
import { NavBar, Icon, Modal } from "antd-mobile";
import { history } from "&/history";
import {Redirect} from "react-router-dom"

export class Head extends Component {
  goBack = leftShow => {
    if (leftShow) {
      history.go(-1);
    }
  };
  gotoSearch = () => {
    history.push("/search");
  };
  render() {
    const { title, leftShow, rightShow, typeShow, mineShow } = this.props;
    return (
      <NavBar
        style={{ position: "relative" }}
        mode="light"
        icon={leftShow && <Icon style={{color:"#333"}} type="left" />}
        onLeftClick={() => this.goBack(leftShow)}
        rightContent={[
          rightShow && (
            <Icon
              key="0"
              type="search"
              style={{ marginRight: "0.2rem", color: "#333" }}
              onClick={this.gotoSearch}
            />
          )
        ]}
      >
        {title}
        {typeShow && (
          <p
            style={{
              position: "absolute",
              right: "0",
              top: "0.3rem",
              fontSize: "0.3rem",
              marginRight: "0.25rem",
              lineHeight: 1
            }}
          >
            只看评论
          </p>
        )}
        {mineShow && (
          <p
            style={{
              color: "#333333",
              position: "absolute",
              right: "0",
              top: "0.3rem"
            }}
          >
            <i
              className="iconfont icon-biaoqing"
              onClick={() =>
                showAlert(
                  "夜间模式",
                  "称为会员后，即可使用带有暗黑主题的夜间模式，分享暗色主题卡片，以及更多高级功能。"
                )
              }
            ></i>
            <i
              className="iconfont icon-xingtaiduICON_sousuo--"
              onClick={() =>
                showAlert(
                  "快速搜索",
                  "称为会员后，即可快速搜索自己创建搜索自己创建/收藏的卡片，以及使用更多高级功能。"
                )
              }
            ></i>
            <i
              className="iconfont icon-shezhi"
              onClick={() =>history.push("/fit")}
            ></i>
          </p>
        )}
      </NavBar>
    );
  }
}

const alert = Modal.alert;
const showAlert = (title, msg) => {
  const alertInstance = alert(title, msg, [
    { text: "取消", onPress: () => console.log("cancel"), style: "default" },
    { text: "了解会员", onPress: () => console.log("ok") }
  ]);
  setTimeout(() => {
    // 可以调用close方法以在外部close
    console.log("auto close");
    alertInstance.close();
  }, 500000);
};
